import { Switch, Route } from "react-router-dom";
import Headers from "./components/Header";
import Home from "./components/Home";
import "./assets/css/home.css";
import About from "./components/About";
import Contact from "./components/Contact";
import TestsPage from "./components/TestsPage";
import Footer from "./components/Footer";
import PersonalityTest from "./components/PersonalityTest";
import CareerTest from "./components/CareerTest";
import PersonalityInstSection from "./components/PersonalityInstSection";
import PersonalityTestResult from "./components/PersonalityTestResult";
export default function App() {
  return (
    <>
      <Headers />
      <main style={{ paddingTop: "50px" }}>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/about" component={About} />
          <Route path="/contact" component={Contact} />
          <Route path="/tests" component={TestsPage} />
          <Route
            path="/personality-test-instructions"
            component={PersonalityInstSection}
          />
          <Route path="/personality-test" component={PersonalityTest} />
          <Route path="/personality-result" component={PersonalityTestResult} />
          <Route path="/career-test" component={CareerTest} />
        </Switch>
      </main>
      <Footer />
    </>
  );
}
