import React from "react";

function PersonalityTestResult() {
  return (
    <section className="test-body">
      <div className="heading container bg-secondary text-light text-center p-3 mt-5">
        <h1 className="text-center">Work-Value Test</h1>
      </div>
      <div className="container result-section text-center" id="result-section">
        <h1 className="text-center  p-2 m-2">Result</h1>

        <div className="container crevativity" id="crevativity">
          <h2>CREATIVITY</h2>
          <p>
            Work which permits one to invent new things, design newproducts, or
            develop new ideas.
          </p>
          <div
            className=" rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="creativity-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container management">
          <h2>MANAGEMENT</h2>
          <p>Work which permits one to plan and lay out work for others.</p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="management-score">{20}</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container achievement">
          <h2>ACHIEVEMENT</h2>
          <p>
            Work which gives one the feeling of accomplishment in doing a job
            well.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="achive-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container surroundings">
          <h2>SURROUNDINGS</h2>
          <p>
            Work which is carried out under pleasant conditions not too hot or
            too cold, noisy, dirty, etc.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="surroundings-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container supervisory-relationships">
          <h2>SUPERVISORY RELATIONSHIPS</h2>
          <p>
            Work which is carried out under a supervisor who is fair and with
            whom one can get along.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="supervisory-relationships-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container way-of-life">
          <h2>WAY OF LIFE</h2>
          <p>
            Work that permits one to live the kind of life he/she chooses and to
            be the type of person he/she wishes to be.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="way-of-life-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container security">
          <h2>SECURITY</h2>
          <p>
            Work that permits one to live the kind of life he/she chooses and to
            be the type of person he/she wishes to be.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="security-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container associates">
          <h2>ASSOCIATES</h2>
          <p>
            Work which brings one into contact with fellow workers whom you
            like.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="associates-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container aesthetic">
          <h2>AESTHETIC</h2>
          <p>
            Work which permits one to make beautiful things and to contribute
            beauty to the world.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="aesthetic-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container prestige">
          <h2>PRESTIGE</h2>
          <p>
            Work which gives one standing on the eyes of others and evokes
            respect.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="prestige-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container independence">
          <h2>INDEPENDENCE</h2>
          <p>
            Work which permits one to work in his/her own way, as fast or slow
            as he/she wishes.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="independence-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container variety">
          <h2>VARIETY</h2>
          <p>
            Work which provides an opportunity to do different types of tasks.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="variety-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container economic-return">
          <h2>ECONOMIC RETURN</h2>
          <p>
            Work which pays well and enables one to have the things he/she
            wants.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="economic-return-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container altrusim">
          <h2>ALTRUISM</h2>
          <p>Work which enables one to contribute to the welfare of others.</p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="altrusim-return-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container intellectual-stimulation">
          <h2>INTELLECTUAL STIMULATION</h2>
          <p>
            Work which provides opportunity for independent thinking and for
            learning how and why things work.
          </p>
          <div
            className="rounded-circle bg-success text-light text-center p-2 fs-3"
            style={{
              margin: "auto",
              borderRadius: "50%",
              width: "100px",
              height: "100px"
            }}
          >
            Score:<div id="intellectual-stimulation-return-score">20</div>
          </div>
        </div>
        <hr className="featurette-divider" />
        <div className="container">
          <a href="pages/personality_test.html" className="againB btn btn-">
            Test Again
          </a>
          <a href="/" className="homeB btn btn-">
            Home Page
          </a>
          {/* <button type="button" onClick={print()} className="printB btn btn-">Print Result</button> */}
        </div>
      </div>
    </section>
  );
}

export default PersonalityTestResult;
