import React from "react";
import { Link } from "react-router-dom";

function PersonalityInstSection() {
  return (
    <section className="test-body">
      <div className="heading container bg-secondary text-light text-center p-3 mt-5">
        <h1 className="text-center">Work-Value Test</h1>
      </div>
      <div className="container text-center shadow p-3 mb-5 rounded mt-5">
        <h1 className="m-2">Instruction Section</h1>
        <p className="lead p-5 introT">
          The statements below represent values which people consider important
          in their work. These are satisfactions which people often seek in
          their jobs or as a result of their jobs. They are not all considered
          equally important; some are very important to some people but of
          little importance to others. Read each statement carefully and
          indicate how important it is to you.
        </p>
        <Link to="/personality-test" className="startB btn mb-5">
          Start Test
        </Link>
      </div>
    </section>
  );
}

export default PersonalityInstSection;
