import React from "react";
import { Link } from "react-router-dom";

export default function TestsPage() {
  return (
    <section style={{ paddingTop: "150px" }}>
      <div className="container">
        <div class="row">
          <div class="col-sm-6">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">Personality Test</h5>
                <p class="card-text">
                  Take this test to know your personal strength and weekness.
                </p>
                <Link
                  href="/personality-test-instructions"
                  class="btn btn-primary"
                >
                  Go somewhere
                </Link>
              </div>
            </div>
          </div>
          <div class="col-sm-6">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">Career Test</h5>
                <p class="card-text">
                  With supporting text below as a natural lead-in to additional
                  content.
                </p>
                <a href="/career-test" class="btn btn-primary">
                  Go somewhere
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
