import "../assets/css/index-top-cards.css";
import img1 from "../assets/images/top-cards/Psychiatry-Med.jpg";
import img2 from "../assets/images/top-cards/alone-girl.jpg";
import img3 from "../assets/images/top-cards/emoje-girl.jpg";

const IndexTopCards = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="card-wrapper col-lg-4 col-md-6 col-xs-12">
          <div className="card">
            <div className="card-img-wrapper">
              <img className="card-img-top" src={img1} alt="Card cap" />
            </div>
            <div className="card-body c-body">
              <h5 className="card-title">Basics of Psychiatry</h5>
              <div className="card-content">
                <p className="card-text">
                  Here We will discuss and try to understand various factors of
                  mental illness. We will also dissuss about how to counter this
                  illness.
                </p>
                <a href="/" className="btn btn-primary">
                  Read More
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className="card-wrapper col-lg-4 col-md-6 col-xs-12">
          <div className="card">
            <div className="card-img-wrapper">
              <img className="card-img-top" src={img2} alt="Card cap" />
            </div>
            <div className="card-body c-body">
              <h5 className="card-title">Basics of Psychology</h5>
              <p className="card-text">
                Here we will try to understand the human Psychology how it is
                developed. We will also get to learn about basic principle of
                psycology.
              </p>
              <a href="/" className="btn btn-primary">
                Read More
              </a>
            </div>
          </div>
        </div>
        <div className="card-wrapper col-lg-4 col-md-6 col-xs-12">
          <div className="card">
            <div className="card-img-wrapper">
              <img className="card-img-top" src={img3} alt="Card cap" />
            </div>
            <div className="card-body c-body">
              <h5 className="card-title">Starting Your Therapy Session.</h5>
              <div className="card-content">
                <p className="card-text">
                  Here will will understand the step by step process of your
                  therapy and counselling sessions. How it works in detail.{" "}
                </p>
                <a href="/" className="btn btn-primary">
                  Read More
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default IndexTopCards;
